* {
  box-sizing: border-box;
}

body, html {
  margin: 0px;
  padding: 0px;
  font-family: Arial, Helvetica, sans-serif;
  
}

.app-container {
  min-height: 100dvh;
  max-width: 100vw;
  display: flex;
  justify-content: center;
  padding: 0;
  background: blue;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.shapesIcon {
  position: absolute;
  top: -52px;
  left: -54px;
  width: 235px;
  height: 173px;
}

.shapesIcon1 {
  position: absolute;
  height: 20.5%;
  width: 60.26%;
  top: 84.24%;
  right: -18.97%;
  bottom: -4.74%;
  left: 58.72%;
}

.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: auto;
  justify-content: center;
  position: relative;
  padding: 0;
  background: red;
  display:none;
}

:root {
  --pr-color: #32cd32;
  --sc-color: rgb(80, 128, 96);
}

/* Styles for screens larger than laptops (1024px and up) */
@media (min-width: 1024px) {
  .page-container {
    width: 512px;
    border: 2px solid black;
    position: relative;
  }
}
